/*============================ 
// Global styles
=============================*/
.no-padding {
  padding-left: 0 !important;
  padding-right: 0 !important;
}
.no-padding__left {
  padding-left: 0 !important;
}
.no-padding__right {
  padding-right: 0 !important;
}

section,
.section {
  position: relative;
  overflow: hidden;
}

@media (min-width: 1920px) {
  .container {
    max-width: 1434px;
  }
}
@keyframes toBottomFromTop {
  49% {
    transform: translateY(-100%);
  }
  50% {
    opacity: 0;
    transform: translateY(100%);
  }
  51% {
    opacity: 1;
  }
}
