/*================================
// para-text/normal text style
=================================*/
.circle-radius {
  border-radius: 50%;
}

.sticky-tab-style {
  position: sticky;
  top: 77px;
  z-index: 1;
  background: white;
  padding: 15px 25px;
  box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
  border-radius: 0px 0px 8px 8px;
  transition: all 0.4s ease-in-out;
}

.animate__animated {
  opacity: 0;
  visibility: hidden;
}

.swiper-vertical {
  max-height: 205px;
}

.marquee {
  width: 100%;
  overflow: hidden;
}

/** Bootstrap Row customize**/
.row {
  row-gap: 24px;
}

.nice-select {
  background: transparent;
  border: none;
  font-family: var(--font-outfit);
  font-weight: 400;
  font-size: 15px;
  color: var(--color-gray12);
  padding-right: 100px;
  text-transform: capitalize;
}
.nice-select .list {
  width: 100%;
}

.nice-select:after {
  border-bottom: 2px solid #8A8A8A;
  border-right: 2px solid #8A8A8A;
  height: 8px;
  width: 8px;
  right: -5px;
  top: 44%;
}

/*=============================
// Bootstrap Row gutter reset
==============================*/
@media (min-width: 1200px) {
  .row.gutters-30 {
    margin-left: -15px;
    margin-right: -15px;
  }
  .row.gutters-30 > [class^=col-] {
    padding-left: 15px;
    padding-right: 15px;
  }
}
@media only screen and (min-width: md min) {
  .swiper {
    padding-bottom: 120px;
  }
}

.swiper-pagination .swiper-pagination-bullet {
  background: #D9D9D9;
  width: 10px;
  height: 10px;
  opacity: 1;
}

.swiper-pagination .swiper-pagination-bullet.swiper-pagination-bullet-active {
  background-color: #12D28C;
}

.header-hero-wrapper {
  background: linear-gradient(92.92deg, #2405F3 9.1%, #5C1CE2 99.72%);
  position: relative;
}
.header-hero-wrapper::before {
  content: "";
  background-image: url("../images/hero-section/opacity-bg.png");
  position: absolute;
  background-size: cover;
  background-position: center;
  width: 100%;
  top: 0;
  bottom: 0;
  left: 0;
}

