  stroke: var(--color-white);
  transition: all 0.4s ease;
  -webkit-transition: all 0.4s ease;
  display: none;
}
.menu--item .second-lavel > .sub--menu {
  left: calc(100% + 1px);
  top: 0;
}
.menu--item .menu--sub {
  position: absolute;
  left: 0;
  top: 100%;
  min-width: 250px;
  max-width: 250px;
  background-color: var(--color-white);
  color: red;
  border-top: none;
  border-radius: 0;
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
