  padding-bottom: 20px;
  transform: scaleY(0);
  transform-origin: 0 0 0;
  transition: all 0.4s cubic-bezier(0.3, 0.02, 0, 0.6);
  visibility: hidden;
  opacity: 0;
  z-index: 9;
  display: block;
  padding-left: 10px;
  transition: all 0.4s ease-in-out;
}
.menu--item .menu--sub .menu--item {
  margin: 0;
  position: relative;
}
.menu--item .menu--sub .menu--item::before {
  content: "";
  position: absolute;
  width: 3px;
  height: 0;
  top: 50%;
  left: 15px;
  transform: translateY(-50%);
  margin: 0;
  background: var(--color-primary);
